import React, { Component } from 'react'
import TextField from 'material-ui/TextField';
import Grid from '@material-ui/core/Grid';
import { typeMusic } from '../Actions';
import { connect } from 'react-redux';
import MusicResults from './MusicResults';

class SearchMusic extends Component {

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.keyword !== this.props.keyword) {
            this.fetchData()
        }
    }

    fetchData = () => {
        fetch(`https://api.soundcloud.com/tracks/?client_id=86b6a66bb2d863f5d64dd8a91cd8de94&q=${this.props.keyword}`)
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error('Something went wrong ...');
                }
            })
            .then(res => this.props.typeMusic(this.props.keyword, res))
            .catch(error => console.log(error));
    }

    handleChange = (e) => {
        const val = e.target.value;
        this.props.typeMusic(val, this.props.music)
    }

    render() {
        return (
            <div className="container">
                <TextField name="searchMusic" value={this.props.keyword} onChange={this.handleChange} floatingLabelText="search for music" fullWidth={true} />
                <br />
                <Grid container spacing={10} style={{ padding: '20px' }}>
                   {this.props.music.map((item, index) => <Grid key={index} item xs={12} sm={6} md={4} lg={4} xl={3}><MusicResults title={item.title} image={item.artwork_url} /></Grid> )} 
                </Grid>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        keyword: state.musicReducer.musicKeyword,
        music: state.musicReducer.music
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        typeMusic: (keyword, music) => { dispatch(typeMusic(keyword, music)) },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SearchMusic);

